#Footer{
    font-family: Montserrat, sans-serif;
}

#Footer nav{
    background-color: #fd611a;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 130px;
    color: #ffffff;
}

#Footer nav > div{
    width: 25%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 20px;
}

#Footer nav > div:not(:first-child){
    border-left: 1px solid #fd8046;
}

#Footer nav :first-child > img{
    width: 70px;
    cursor: pointer;
}




#Footer  nav > :nth-child(2) > h1{
    font-size: 16px;
    font-weight: 500;
    margin-bottom: 6px;
}
#Footer  nav > :nth-child(2) > p{
    font-size: 15px;
    font-weight: 200;
    margin-bottom: 15px;
}
#Footer  nav > :nth-child(2) > a{
    font-size: 13px;
    border: 1px solid #fefefe;
    text-decoration: none;
    color: #fefefe;
    padding: 5px;
    border-radius: 5px;
    cursor: pointer;
}

#Footer  nav > :nth-child(2) > a:hover{
    background-color: #fefefe;
    color: #fd8046;
}


#Footer  nav > :nth-child(3) a {
    margin: 10px 0;
    text-decoration: none;
}
#Footer  nav > :nth-child(3) :first-child{
    border: 1px solid #ffffff52;
    width: 200px;
    height: 37px;
    text-align: center;
    border-radius: 5px;
    font-size: 15px;
    color: #fefefefe;
    padding: 5px;
} 
#Footer  nav > :nth-child(3) :first-child:hover{
    background-color: #fefefe;
    color: #fd8046;
}

#Footer  nav > :nth-child(3) :nth-child(2){
    color: #fefefe;
    text-decoration: underline;
}
#Footer  nav > :nth-child(3) :nth-child(2):hover{
    text-decoration: none;
}

#Footer  nav > :nth-child(4){
    align-items: start;
    padding-left: 50px;
}

#Footer  nav > :nth-child(4) div img{
    width: 40px;
    height: 40px;
    margin: 5px 10px 0 0;
    cursor: pointer;
}


#Footer > p{
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 5px 0;
    font-size: 12px;
    background-color: #eeeeee;
    color: #333;
    font-weight: 400;
    line-height: 14px;
    font-family:  sans-serif ; 
}